.burger {
    position: fixed;
    top: 8px;
    right: 15px;
    width: 40px;
    height: 40px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    z-index: 2;
    cursor: pointer;  
  }
  .burger span {
    position: absolute;
    width: 100%;
    height: 6px;
    background: black;
    left: 0;
    -webkit-transition: top 0.3s 0.2s, bottom 0.3s 0.2s, opacity 0.3s, -webkit-transform 0.3s 0.3s;
    transition: top 0.3s 0.2s, bottom 0.3s 0.2s, opacity 0.3s, -webkit-transform 0.3s 0.3s;
    transition: top 0.3s 0.2s, bottom 0.3s 0.2s, opacity 0.3s, transform 0.3s 0.3s;
    transition: top 0.3s 0.2s, bottom 0.3s 0.2s, opacity 0.3s, transform 0.3s 0.3s, -webkit-transform 0.3s 0.3s;
    cursor: pointer;
  }
  .burger span:nth-child(1) {
    top: 5px;
  }
  .burger.active span:nth-child(1) {
    top: calc(50% - 5px);
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  
  .burger span:nth-child(2) {
    top: calc(50% - 3px);
  }
  
  .burger.active span:nth-child(2) {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    top: calc(50% - 5px);
  }
  .burger span:nth-child(3) {
    bottom: 5px;
  }
  .burger.active span:nth-child(3) {
    bottom: calc(50% - 3px);
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  .navAccesOff{
    
    display: none;   
