  .navAccesOnn{
    -webkit-animation: navAnimOnn .4s linear;
            animation: navAnimOnn .4s linear;
    display: block;
  }

@-webkit-keyframes navAnimOnn{
0%{
  opacity: 0
}
50%{
  opacity: .5
}
100%{
  opacity: 1;
  -webkit-transform: translatey(100vh);
          transform: translatey(100vh)
}
}

@keyframes navAnimOnn{
0%{
  opacity: 0
}
50%{
  opacity: .5
}
100%{
  opacity: 1;
  -webkit-transform: translatey(100vh);
          transform: translatey(100vh)
}
}
